import React from "react";

function CardComponent(props) {
  return (
    <div className="card">
      <img src = { props.name.image } className="course-image u-full-width" alt="course"></img>
      <div className="info-card">
        <h4>{ props.name.title }</h4>
        <p>{ props.name.author }</p>
        <img src="images/stars.png" alt="stars"></img>
        <div className="price-info">
          <span className ="price">{ props.name.price }</span>
          <span className ="current-price">{ props.name.currentPrice }</span>
        </div>
        <a href="#" className="u-full-width button-primary button input add-to-cart" data-id={ props.name.id }>Add to Cart</a>
      </div>
    </div>
  );
}

export default CardComponent;